const {
  verifyCaptcha
} = require('../../lib/utils/captcha')
const {
  EMAIL_SCENE
} = require('../../common/constants')
const {
  ERROR
} = require('../../common/error')
/**
 * 
 * @tutorial
 * @param {Object} params
 * @param {String} params.email     
 * @param {String} params.captcha   
 * @param {String} params.scene     
 * @returns
 */
module.exports = async function (params = {}) {
  const schema = {
    email: 'email',
    captcha: 'string',
    scene: 'string'
  }
  this.middleware.validate(params, schema)

  const {
    email,
    captcha,
    scene
  } = params

  if (!(Object.values(EMAIL_SCENE).includes(scene))) {
    throw {
      errCode: ERROR.INVALID_PARAM
    }
  }

  await verifyCaptcha.call(this, {
    scene: 'send-email-code',
    captcha
  })

  // -- 
  await require('../../lib/utils/verify-code')
    .setEmailVerifyCode.call(this, {
      email,
      code: '123456',
      expiresIn: 180,
      scene
    })
  return {
    errCode: 'uni-id-invalid-mail-template',
    errMsg: `123456\n`
  }
  // -- 


  //--
}
