import passwordMod from '@/uni_modules/uni-id-pages/common/password.js'
export default {
	"username": {
		"rules": [{
				required: true,
				errorMessage: '',
			},
			{
				minLength: 3,
				maxLength: 32,
				errorMessage: ' {minLength}  {maxLength} ',
			},
			{
				validateFunction: function(rule, value, data, callback) {
					// console.log(value);
					if (/^1\d{10}$/.test(value) || /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/.test(value)) {
						callback('')
					};
					if (/^\d+$/.test(value)) {
						callback('')
					};
					if(/[\u4E00-\u9FA5\uF900-\uFA2D]{1,}/.test(value)){
						callback('')
					}
					return true
				}
			}
		],
		"label": ""
	},
	"nickname": {
		"rules": [{
				minLength: 3,
				maxLength: 32,
				errorMessage: ' {minLength}  {maxLength} ',
			},
			{
				validateFunction: function(rule, value, data, callback) {
					// console.log(value);
					if (/^1\d{10}$/.test(value) || /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/.test(value)) {
						callback('')
					};
					if (/^\d+$/.test(value)) {
						callback('')
					};
					if(/[\u4E00-\u9FA5\uF900-\uFA2D]{1,}/.test(value)){
						callback('')
					}
					return true
				}
			}
		],
		"label": ""
	},
	...passwordMod.getPwdRules()
}
