const {
  initQQ
} = require('../../lib/third-party/index')
const {
  ERROR
} = require('../../common/error')
const {
  preUnifiedLogin,
  postUnifiedLogin
} = require('../../lib/utils/unified-login')
const {
  LOG_TYPE
} = require('../../common/constants')
const {
  getQQPlatform,
  generateQQCache,
  saveQQUserKey
} = require('../../lib/utils/qq')
const url = require('url')

/**
 * QQ
 * @tutorial https://uniapp.dcloud.net.cn/uniCloud/uni-id-pages.html#login-by-qq
 * @param {Object} params
 * @param {String} params.code                  QQcode
 * @param {String} params.accessToken           AppQQaccessToken
 * @param {String} params.accessTokenExpired    accessTokenAppQQexpires_in
 * @param {String} params.inviteCode            
 * @returns
 */
module.exports = async function (params = {}) {
  const schema = {
    code: {
      type: 'string',
      required: false
    },
    accessToken: {
      type: 'string',
      required: false
    },
    accessTokenExpired: {
      type: 'number',
      required: false
    },
    inviteCode: {
      type: 'string',
      required: false
    }
  }
  this.middleware.validate(params, schema)
  const {
    code,
    accessToken,
    accessTokenExpired,
    inviteCode
  } = params
  const {
    appId
  } = this.getUniversalClientInfo()
  const qqApi = initQQ.call(this)
  const qqPlatform = getQQPlatform.call(this)
  let apiName
  switch (qqPlatform) {
    case 'mp':
      apiName = 'code2Session'
      break
    case 'app':
      apiName = 'getOpenidByToken'
      break
    default:
      throw new Error('Unsupported qq platform')
  }
  let getQQAccountResult
  try {
    getQQAccountResult = await qqApi[apiName]({
      code,
      accessToken
    })
  } catch (error) {
    console.error(error)
    await this.middleware.uniIdLog({
      success: false,
      type: LOG_TYPE.LOGIN
    })
    throw {
      errCode: ERROR.GET_THIRD_PARTY_ACCOUNT_FAILED
    }
  }

  const {
    openid,
    unionid,
    // 
    sessionKey // QQsessionKey
  } = getQQAccountResult

  const {
    type,
    user
  } = await preUnifiedLogin.call(this, {
    user: {
      qq_openid: {
        [qqPlatform]: openid
      },
      qq_unionid: unionid
    }
  })
  const extraData = {
    qq_openid: {
      [`${qqPlatform}_${appId}`]: openid
    },
    qq_unionid: unionid
  }
  if (type === 'register' && qqPlatform !== 'mp') {
    const {
      nickname,
      avatar
    } = await qqApi.getUserInfo({
      accessToken,
      openid
    })
    if (avatar) {
      // eslint-disable-next-line n/no-deprecated-api
      const extName = url.parse(avatar).pathname.split('.').pop()
      const cloudPath = `user/avatar/${openid.slice(-8) + Date.now()}-avatar.${extName}`
      const getAvatarRes = await uniCloud.httpclient.request(avatar)
      if (getAvatarRes.status >= 400) {
        throw {
          errCode: ERROR.GET_THIRD_PARTY_USER_INFO_FAILED
        }
      }
      const {
        fileID
      } = await uniCloud.uploadFile({
        cloudPath,
        fileContent: getAvatarRes.data
      })
      extraData.avatar_file = {
        name: cloudPath,
        extname: extName,
        url: fileID
      }
    }
    extraData.nickname = nickname
  }
  await saveQQUserKey.call(this, {
    openid,
    sessionKey,
    accessToken,
    accessTokenExpired
  })
  return postUnifiedLogin.call(this, {
    user,
    extraData: {
      ...extraData,
      ...generateQQCache.call(this, {
        openid,
        sessionKey, // QQsessionKey
        accessToken, // AppQQaccessToken
        accessTokenExpired // AppQQaccessToken
      })
    },
    isThirdParty: true,
    type,
    inviteCode
  })
}
