const {
  preLoginWithPassword,
  postLogin
} = require('../../lib/utils/login')
const {
  getNeedCaptcha,
  verifyCaptcha
} = require('../../lib/utils/captcha')
const {
  CAPTCHA_SCENE
} = require('../../common/constants')
const {
  ERROR
} = require('../../common/error')

/**
 * 
 * @tutorial https://uniapp.dcloud.net.cn/uniCloud/uni-id-pages.html#login
 * @param {Object} params
 * @param {String} params.username  
 * @param {String} params.mobile    
 * @param {String} params.email     
 * @param {String} params.password  
 * @param {String} params.captcha   
 * @returns
 */
module.exports = async function (params = {}) {
  const schema = {
    username: {
      required: false,
      type: 'username'
    },
    mobile: {
      required: false,
      type: 'mobile'
    },
    email: {
      required: false,
      type: 'email'
    },
    password: 'password',
    captcha: {
      required: false,
      type: 'string'
    }
  }
  this.middleware.validate(params, schema)
  const {
    username,
    mobile,
    email,
    password,
    captcha
  } = params
  if (!username && !mobile && !email) {
    throw {
      errCode: ERROR.INVALID_USERNAME
    }
  } else if (
    (username && email) ||
    (username && mobile) ||
    (email && mobile)
  ) {
    throw {
      errCode: ERROR.INVALID_PARAM
    }
  }
  const needCaptcha = await getNeedCaptcha.call(this, {
    username,
    mobile,
    email
  })
  if (needCaptcha) {
    await verifyCaptcha.call(this, {
      captcha,
      scene: CAPTCHA_SCENE.LOGIN_BY_PWD
    })
  }
  const {
    user,
    extraData
  } = await preLoginWithPassword.call(this, {
    user: {
      username,
      mobile,
      email
    },
    password
  })
  return postLogin.call(this, {
    user,
    extraData
  })
}
