const {
  postRegister,
  preRegisterWithPassword
} = require('../../lib/utils/register')
const {
  verifyCaptcha
} = require('../../lib/utils/captcha')
const {
  CAPTCHA_SCENE,
  EMAIL_SCENE,
  LOG_TYPE
} = require('../../common/constants')
const {
  verifyEmailCode
} = require('../../lib/utils/verify-code')

/**
 * +
 * @tutorial https://uniapp.dcloud.net.cn/uniCloud/uni-id-pages.html#register-user-by-email
 * @param {Object} params
 * @param {String} params.email    
 * @param {String} params.password      
 * @param {String} params.nickname    
 * @param {String} params.code  
 * @param {String} params.inviteCode  
 * @returns
 */
module.exports = async function (params = {}) {
  const schema = {
    email: 'email',
    password: 'password',
    nickname: {
      required: false,
      type: 'nickname'
    },
	code: 'string',
    inviteCode: {
      required: false,
      type: 'string'
    }
  }
  this.middleware.validate(params, schema)
  const {
    email,
    password,
    nickname,
    code,
	inviteCode
  } = params
  
  try {
    // 
    await verifyEmailCode({
      email,
      code,
      scene: EMAIL_SCENE.REGISTER
    })
  } catch (error) {
    await this.middleware.uniIdLog({
      data: {
        email
      },
      type: LOG_TYPE.REGISTER,
      success: false
    })
    throw error
  }
  
  const {
    user,
    extraData
  } = await preRegisterWithPassword.call(this, {
    user: {
      email
    },
    password
  })
  return postRegister.call(this, {
    user,
    extraData: {
      ...extraData,
      nickname,
	  email_confirmed: 1
    },
    inviteCode
  })
}
