const {
  sendSmsCode
} = require('../../lib/utils/sms')
const {
  verifyCaptcha
} = require('../../lib/utils/captcha')
const {
  SMS_SCENE
} = require('../../common/constants')
const {
  ERROR
} = require('../../common/error')

/**
 * 
 * @tutorial https://uniapp.dcloud.net.cn/uniCloud/uni-id-pages.html#send-sms-code
 * @param {Object} params
 * @param {String} params.mobile    
 * @param {String} params.captcha   
 * @param {String} params.scene     
 * @returns
 */
module.exports = async function (params = {}) {
  const schema = {
    mobile: 'mobile',
    captcha: 'string',
    scene: 'string'
  }
  this.middleware.validate(params, schema)
  const {
    mobile,
    captcha,
    scene
  } = params
  if (!(Object.values(SMS_SCENE).includes(scene))) {
    throw {
      errCode: ERROR.INVALID_PARAM
    }
  }
  await verifyCaptcha.call(this, {
    scene: 'send-sms-code',
    captcha
  })

  // -- 
  const {
    templateId
  } = (this.config.service &&
    this.config.service.sms &&
    this.config.service.sms.scene &&
    this.config.service.sms.scene[scene]) || {}
  if (!templateId) {
    await require('../../lib/utils/verify-code')
      .setMobileVerifyCode.call(this, {
        mobile: params.mobile,
        code: '123456',
        expiresIn: 180,
        scene
      })
    return {
      errCode: 'uni-id-invalid-sms-template-id',
      errMsg: `scene=${scene},templateId\n123456\n/common/uni-config-center/uni-id/config.jsonservice->sms\nhttps://uniapp.dcloud.io/uniCloud/uni-id.html#config`
    }
  }
  // -- 

  return sendSmsCode.call(this, {
    mobile,
    scene
  })
}
