const {
  getNeedCaptcha,
  verifyCaptcha
} = require('../../lib/utils/captcha')
const {
  LOG_TYPE,
  SMS_SCENE,
  CAPTCHA_SCENE
} = require('../../common/constants')
const {
  verifyMobileCode
} = require('../../lib/utils/verify-code')
const {
  preBind,
  postBind
} = require('../../lib/utils/relate')

/**
 * 
 * @tutorial https://uniapp.dcloud.net.cn/uniCloud/uni-id-pages.html#bind-mobile-by-sms
 * @param {Object} params
 * @param {String} params.mobile    
 * @param {String} params.code      
 * @param {String} params.captcha   
 * @returns
 */
module.exports = async function (params = {}) {
  const schema = {
    mobile: 'mobile',
    code: 'string',
    captcha: {
      type: 'string',
      required: false
    }
  }
  const {
    mobile,
    code,
    captcha
  } = params
  this.middleware.validate(params, schema)
  const uid = this.authInfo.uid

  // 
  const needCaptcha = await getNeedCaptcha.call(this, {
    uid,
    type: LOG_TYPE.BIND_MOBILE
  })
  if (needCaptcha) {
    await verifyCaptcha.call(this, {
      captcha,
      scene: CAPTCHA_SCENE.BIND_MOBILE_BY_SMS
    })
  }

  try {
    // 
    await verifyMobileCode({
      mobile,
      code,
      scene: SMS_SCENE.BIND_MOBILE_BY_SMS
    })
  } catch (error) {
    await this.middleware.uniIdLog({
      data: {
        user_id: uid
      },
      type: LOG_TYPE.BIND_MOBILE,
      success: false
    })
    throw error
  }
  const bindAccount = {
    mobile
  }
  await preBind.call(this, {
    uid,
    bindAccount,
    logType: LOG_TYPE.BIND_MOBILE
  })
  await postBind.call(this, {
    uid,
    bindAccount,
    extraData: {
      mobile_confirmed: 1
    },
    logType: LOG_TYPE.BIND_MOBILE
  })
  return {
    errCode: 0
  }
}
